.modal {
    display: none;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,.7);
    z-index: 999;
    backdrop-filter:blur(20px) saturate(280%);

    @include mobile {
        z-index: 9999999;
    }

    & .close_btn {
        background: rgba(255,255,255,0.2);
        position: absolute;
        top: 82px;
        right: 16px;
        color: #fff;
        padding: 10px 28px;
        border-radius: 50px;
        font-size: 14px;

        @include mobile {
            top: 16px;
            background: rgba(0, 0, 0, 0.5);
        }
    }

    &.picture {
        align-items: center;
        justify-content: center;

        & img {
            height: 100%;
        }
    }
}